.sandwich {
  height: 4em;
  width: 4em;
  margin-top: -1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: .85;
  transition: .1s ease-out all;
  cursor: pointer; }
  .sandwich:hover {
    opacity: 1; }
    .sandwich:hover .sandwich-slice:nth-child(1) {
      width: 2em;
      margin-left: 0; }
    .sandwich:hover .sandwich-slice:nth-child(3) {
      width: 2em;
      margin-right: 0; }
  .sandwich.active .sandwich-slice {
    margin: 0; }
    .sandwich.active .sandwich-slice:nth-child(1) {
      transform: rotate(45deg);
      margin-bottom: -2px;
      width: 2em;
      margin-left: 0; }
    .sandwich.active .sandwich-slice:nth-child(2) {
      transform: scale(0);
      opacity: .25; }
    .sandwich.active .sandwich-slice:nth-child(3) {
      transform: rotate(-45deg);
      margin-top: -2px;
      width: 2em;
      margin-right: 0; }
  .sandwich .sandwich-slice {
    background: white;
    width: 2em;
    height: 2px;
    margin: .25em;
    transition: .1s ease-out all; }
    .sandwich .sandwich-slice:nth-child(1) {
      width: 1em;
      margin-left: 1em; }
    .sandwich .sandwich-slice:nth-child(3) {
      width: 1em;
      margin-right: 1em; }

